import React, { Component } from 'react';
import { Carousel } from 'react-responsive-carousel';
import 'react-responsive-carousel/lib/styles/carousel.min.css';

class PostCarousel extends Component {
  render() {
    return (
      <Carousel
        showThumbs={false}
        showStatus={false}                  
      >
        <div>
          <img src={`/assets/${this.props.post.images[0]}`} alt="레슨 안내 이미지" />
        </div>
        <div>
          <img src={`/assets/${this.props.post.images[1]}`} alt="레슨 안내 이미지" />
        </div>
        <div>
          <img src={`/assets/${this.props.post.images[2]}`} alt="레슨 안내 이미지" />
        </div>
        <div>
          <img src={`/assets/${this.props.post.images[3]}`} alt="레슨 안내 이미지" />
        </div>
        <div>
          <img src={`/assets/${this.props.post.images[4]}`} alt="레슨 안내 이미지" />
        </div>
      </Carousel>
    );
  }
};

export default PostCarousel;


